/*
 *  Team carousel
 *    Styles for the team members carousel component.
 *
 *  Version: 1.0.0
 *    1.0.0 - 2021/11/22
 */

.c-team-carousel {
  background-color: $s-color--background-subdued;

  &__inner {
    padding: 5.5rem 0 4.0625rem;
    overflow: hidden;
  }

  &__title {
    text-align: center;
    margin-bottom: 0.625rem;
  }

  &__subheading {
    text-align: center;
    width: 70%;
    margin: 0 auto;
  }

  &__slider {
    display: flex;
    overflow: auto;
    margin-top: 1.5rem;
    scrollbar-width: none;
    scrollbar-color: $s-color--background-subdued $s-color--background-subdued;

    &.keen-slider {
      gap: initial;
    }

    &::-webkit-scrollbar {
      width: 0;
    }

    &::-webkit-scrollbar-track {
      background: $s-color--background-subdued;
    }

    &::-webkit-scrollbar-thumb {
      background: $s-color--background-subdued;
    }
  }

  &__nav {
    display: none;
  }

  //Slides
  &__slide {
    min-width: 88%;

    &.is-active {
      overflow: visible;
    }
  }

  &__slide-inner {
    padding: 2.625rem $s-gutter--edge-mobile 0;
  }

  .keen-slider {
    .c-team-carousel__image-container {
      padding-bottom: 103%;
      transform: translateY(0);
    }

    .is-next-active .c-team-carousel__image-container {
      transform: translateY(-2.75rem);
    }
  }

  &__image-container {
    transition: transform 0.5s;
  }

  &__slide-content {
    opacity: 0;
    transition: all 0.2s;
    width: 135%;

    .is-active & {
      opacity: 1;
      width: 135%;
    }
  }

  &__name {
    margin: 2.375rem 0 1rem;
  }

  &__subtitle {
    color: $s-color--border-focus;
    font-size: 1rem;
    font-family: $s-font--heading-family;
    margin-bottom: 0.625rem;
  }

  &__member-details {
    display: flex;
    color: $s-color--border-focus;
    font-size: 0.9375rem;
    margin-bottom: 1.5rem;
  }

  &__role {
    padding-right: 1rem;
    border-right: 1px solid currentColor;
  }

  &__years {
    padding: 0 1rem;
    border-right: 1px solid currentColor;
  }

  &__pronouns {
    padding-left: 1rem;
  }

  &__bio {
    margin-top: 1.5rem;
    margin-bottom: 0;
    font-size: 0.9375rem;
    font-weight: 400;
  }

  @include t-media-query($s-small-up) {
    &__slider-container {
      position: relative;
    }

    &__slider {
      justify-content: center;

      &.keen-slider {
        justify-content: initial;
      }
    }

    &__slide {
      min-width: 33%;
    }

    &__subheading {
      width: 35%;
      margin: 0 auto;
    }

    .keen-slider {
      .c-team-carousel__image-container {
        transform: translateY(0);
        transition: all 0.5s;
      }

      .is-next-active .c-team-carousel__image-container,
      .is-prev-active .c-team-carousel__image-container {
        transform: translateY(-2.125rem);
      }

      .is-2-next-active .c-team-carousel__image-container,
      .is-2-prev-active .c-team-carousel__image-container {
        transform: translateY(-4.125rem);
      }


      &+.c-team-carousel__nav {
        display: flex;
        justify-content: space-between;
        width: 25rem;
        @include t-absolute-center-x-y();
        top: 0;
        margin-top: calc(4.9375rem + 30.5%);
        pointer-events: none;
      }
    }

    &__slide-content {
      opacity: 0;
      transition: all 0.5s;
      text-align: center;
      display: grid;
      justify-items: center;
      transform: translateX(-25%);
      width: 200%;

      .is-active & {
        opacity: 1;
        width: 200%;
        transform: translateX(-25%);
      }
    }

    &__nav-arrow img {
      width: 2rem;
      height: 2rem;
      padding: 0.625rem;
      cursor: pointer;
      pointer-events: all;
    }

    &__member-details {
      margin-bottom: 1.625rem;
    }

    &__bio {
      margin-top: 0;
    }
  }

  @include t-media-query($s-medium-up) {
    &__inner {
      padding-top: 7.3125rem;
    }

    &__subheading {
      width: 35%;
      margin: 0 auto;
    }

    &__slide {
      min-width: 21.3%;
    }

    .keen-slider {
      .c-team-carousel__image-container {
        transform: translateY(-6.125rem);
        transition: all 0.5s;
      }

      .is-active .c-team-carousel__image-container {
        transform: translateY(0);
      }

      .is-next-active .c-team-carousel__image-container,
      .is-prev-active .c-team-carousel__image-container {
        transform: translateY(-2.125rem);
      }

      .is-2-next-active .c-team-carousel__image-container,
      .is-2-prev-active .c-team-carousel__image-container {
        transform: translateY(-4.125rem);
      }

      &+.c-team-carousel__nav {
        margin-top: calc(7.6875rem + 22%);
        width: 35rem;
      }
    }

    &__slider {
      gap: 1.25rem;

      &.keen-slider {
        overflow: visible !important; //trump keen
      }
    }

    .keen-slider &__slide {
      overflow: visible !important; //trump keen
    }

    &__slide-content {
      opacity: 0;
      transition: all 0.5s;
      text-align: center;
      display: grid;
      justify-items: center;
      transform: translateX(-25%);
      width: 200%;

      .is-active & {
        opacity: 1;
        width: 200%;
        transform: translateX(-25%);
      }
    }

    &__title {
      margin-bottom: 1.25rem;
      font-size: 3rem;
    }

    &__subheading {
      max-width: 31rem;
    }

    &__slider {
      margin-top: 4.375rem;
    }

    &__name {
      font-size: 3rem;
      margin: 5.0625rem 0 1.375rem;
    }

    &__subtitle {
      margin-bottom: 0.625rem;
    }

    &__bio {
      margin-top: 0.375rem;
      font-size: 0.9375rem;
    }

    &__nav-arrow img {
      width: 2.625rem;
      height: 2.625rem;
      padding: 0.625rem;
    }
  }

  @include t-media-query($s-xlarge-up) {
    &__inner {
      padding-top: 10.5rem;
      padding-bottom: 4.75rem;
    }

    .keen-slider {
      .c-team-carousel__image-container {
        padding-bottom: 107%;
        transform: translateY(-9.125rem);
      }

      .is-active .c-team-carousel__image-container {
        transform: translateY(0);
      }

      .is-next-active .c-team-carousel__image-container,
      .is-prev-active .c-team-carousel__image-container {
        transform: translateY(-3.5rem);
      }

      .is-2-next-active .c-team-carousel__image-container {
        transform: translate(0.3125rem, -7.25rem);
      }

      .is-2-prev-active .c-team-carousel__image-container {
        transform: translate(-0.3125rem, -7.25rem);
      }

      &+.c-team-carousel__nav {
        margin-top: calc(7.8125rem + 23.5%);
      }

    }

    &__slider {
      gap: 4.6875rem;
    }

    &__slide-inner {
      padding: 2.75rem 0 0;
    }

    &__slide-content {
      width: 172%;
      transform: translateX(-21%);

      .is-active & {
        width: 172%;
        transform: translateX(-21%);
      }
    }

    &__subheading {
      margin-bottom: 2rem;
    }
  }
}